/* 🔘 FAB-кнопка */
.quick-notes-fab {
  position: fixed;
  bottom: 24px;
  right: 24px;
  width: 56px;
  height: 56px;
  border-radius: 50%;
  background-color: #4e4eeb;
  color: white;
  font-size: 24px;
  display: flex;
  justify-content: center;
  align-items: center;
  border: none;
  cursor: pointer;
  z-index: 1001;
  box-shadow: 0 6px 16px rgba(0, 0, 0, 0.3);
  transition: background 0.3s ease, transform 0.2s ease;
}

.quick-notes-fab:hover {
  background-color: #3b3bd6;
  transform: scale(1.08);
}

body.dark .quick-notes-fab {
  background-color: #3a3aee;
}

body.dark .quick-notes-fab:hover {
  background-color: #5050ff;
}

/* 🔲 Панель */
.quick-notes-panel {
  position: fixed;
  top: 0;
  right: 0;
  width: 340px;
  height: 100vh;
  background: #ffffff;
  padding: 16px;
  display: flex;
  flex-direction: column;
  z-index: 1000;
  box-shadow: -4px 0 16px rgba(0, 0, 0, 0.2);
  overflow: hidden;
  border-left: 1px solid #ddd;
}

body.dark .quick-notes-panel {
  background: #1e1e2a;
  color: #f0f0f0;
  border-left: 1px solid #333;
  box-shadow: -4px 0 20px rgba(0, 0, 0, 0.5);
}

/* 🔠 Хедер */
.quick-notes-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 12px;
}

.quick-notes-header h4 {
  margin: 0;
  font-size: 18px;
}

.quick-notes-header button {
  background: transparent;
  border: none;
  font-size: 20px;
  cursor: pointer;
  padding: 6px 10px;
  border-radius: 6px;
  transition: background 0.2s ease;
  color: #444;
}

.quick-notes-header button:hover {
  background-color: rgba(0, 0, 0, 0.06);
}

body.dark .quick-notes-header button {
  color: #ccc;
}

body.dark .quick-notes-header button:hover {
  background-color: rgba(255, 255, 255, 0.08);
}

/* 📋 Список */
.quick-notes-list {
  flex: 1;
  overflow-y: auto;
  display: flex;
  flex-direction: column;
  gap: 10px;
  padding-right: 6px;
}

/* 📝 Швидка нотатка */
.quick-note {
  padding: 10px 14px;
  border-radius: 10px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.08);
  font-size: 14px;
  word-wrap: break-word;
  display: flex;
  flex-direction: column;
  gap: 6px;
  color: #111;
}

/* 🌕 Світла тема категорій */
.quick-note.робота     { background-color: #e3f2fd; }
.quick-note.навчання   { background-color: #e8f5e9; }
.quick-note.особисте   { background-color: #ffebee; }
.quick-note.інше       { background-color: #f3e5f5; }
.quick-note.завдання   { background-color: #fff8e1; }
.quick-note.натхнення  { background-color: #ede7f6; }

/* 🌑 Темна тема — ті ж кольори, як у Notes */
body.dark .quick-note.робота     { background-color: #203c5a; color: #fff; }
body.dark .quick-note.навчання   { background-color: #224d36; color: #fff; }
body.dark .quick-note.особисте   { background-color: #4b2c2c; color: #fff; }
body.dark .quick-note.інше       { background-color: #402d4f; color: #fff; }
body.dark .quick-note.завдання   { background-color: #4a3b23; color: #fff; }
body.dark .quick-note.натхнення  { background-color: #3a2b52; color: #fff; }

/* 🖼️ Фото */
.quick-note img {
  max-width: 100%;
  border-radius: 6px;
  margin-top: 6px;
}

/* 📅 Дата */
.quick-date {
  font-size: 12px;
  color: #555;
  text-align: right;
}

body.dark .quick-date {
  color: #ccc;
}

/* ❗ Повідомлення */
.no-notes {
  text-align: center;
  color: #777;
  margin-top: 20px;
}
